import React, { useState } from 'react';
//import { useLocation } from 'react-router-dom';  // For getting data passed via navigate

const Flight = () => {
  //const location = useLocation();
  //const { fromAirport, toAirport, departureDate, returnDate } = location.state || {}; // Getting data passed from Home page

  const [selectedAirline, setSelectedAirline] = useState('');
  const [selectedTimeSlot, setSelectedTimeSlot] = useState('');
  const [passengerDetails, setPassengerDetails] = useState({
    name: '',
    age: '',
    gender: 'Male',
    passengers: 1
  });

  // Airlines and time slots data
  const airlines = ['Airline A', 'Airline B', 'Airline C', 'Airline D', 'Airline E'];
  const timeSlots = ['Morning', 'Afternoon', 'Evening', 'Night'];

  const handleAirlineChange = (e) => {
    setSelectedAirline(e.target.value);
  };

  const handleTimeSlotChange = (e) => {
    setSelectedTimeSlot(e.target.value);
  };

  const handlePassengerChange = (e) => {
    const { name, value } = e.target;
    setPassengerDetails(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const totalAmount = 6453 * passengerDetails.passengers;  // Just an example, you can replace this with real pricing logic

  return (
    <div>
      <h2>Select a Flight</h2>

      <div>
        <label>Airline:</label>
        <select value={selectedAirline} onChange={handleAirlineChange}>
          {airlines.map((airline, index) => (
            <option key={index} value={airline}>{airline}</option>
          ))}
        </select>
      </div>
      
      <div>
        <label>Time Slot:</label>
        <select value={selectedTimeSlot} onChange={handleTimeSlotChange}>
          {timeSlots.map((timeSlot, index) => (
            <option key={index} value={timeSlot}>{timeSlot}</option>
          ))}
        </select>
      </div>

      <h3>Passenger Details</h3>
      <div>
        <label>Name:</label>
        <input
          type="text"
          name="name"
          value={passengerDetails.name}
          onChange={handlePassengerChange}
        />
      </div>

      <div>
        <label>Age:</label>
        <input
          type="number"
          name="age"
          value={passengerDetails.age}
          onChange={handlePassengerChange}
        />
      </div>

      <div>
        <label>Gender:</label>
        <select
          name="gender"
          value={passengerDetails.gender}
          onChange={handlePassengerChange}
        >
          <option value="Male">Male</option>
          <option value="Female">Female</option>
          <option value="Other">Other</option>
        </select>
      </div>

      <div>
        <label>Number of Passengers:</label>
        <input
          type="number"
          name="passengers"
          value={passengerDetails.passengers}
          onChange={handlePassengerChange}
        />
      </div>

      <h4>Total Amount: {totalAmount}</h4>

      <button>Proceed to Payment</button>
    </div>
  );
};

export default Flight;
