import React from 'react';
import { Navigate, Route, Routes } from 'react-router-dom';
import Home from '../pages/Home';
import About from '../pages/About';
import Tours from '../pages/Tours'; 
import Flight from '../pages/Flight'; // Your Flight Page
import Train from '../pages/Train'; // Your Train Page
import Bus from '../pages/Bus'; // Your Bus Page
import Hotel from '../pages/Hotel'; // Your Hotel Page
import TourDetails from '../pages/TourDetails';
import Login from '../pages/Login';
import Register from '../pages/Register';
import SearchResultList from '../pages/SearchResultList';
import ThankYou from '../pages/ThankYou';
import Gallery from '../pages/Gallery';
import ConfirmationPage from '../pages/ConfirmationPage'; // Your Confirmation Page


const Routers = () => {
  return (
    <div>
      <Routes>
        {/* Redirect from root to home page */}
        <Route path='/' element={<Navigate to='/home' />} />
        
        {/* General Pages */}
        <Route path='/home' element={<Home />} />
        <Route path='/about' element={<About />} />
        <Route path='/tours' element={<Tours />} />
        <Route path='/login' element={<Login />} />
        <Route path='/register' element={<Register />} />
        <Route path='/thank-you' element={<ThankYou />} />
        <Route path='/tours/search' element={<SearchResultList />} />
        <Route path='/gallery' element={<Gallery />} />

        {/* Flight, Train, Bus, and Hotel Booking Pages */}
        <Route path='/flight' element={<Flight />} />
        <Route path='/train' element={<Train />} />
        <Route path='/bus' element={<Bus />} />
        <Route path='/hotel' element={<Hotel />} />
        {/* Tour Details Page */}
        <Route path='/tours/:id' element={<TourDetails />} />

        {/* Confirmation Page after booking */}
        <Route path='/confirmation' element={<ConfirmationPage />} />
      </Routes>
    </div>
  );
};

export default Routers;
