import React, { useState, useEffect } from 'react';
import '../styles/home.css'; // Keep your existing styles intact
import { Container, Row, Col } from 'reactstrap';
import worldImg from '../assets/images/world.png';
import heroImg from '../assets/images/hero-img01.jpg';
import heroImg02 from '../assets/images/hero-img02.jpg';
import heroVideo from '../assets/images/hero-video.mp4';
import Subtitle from '../shared/Subtitle';

// Importing flatpickr and its CSS
import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';

const Home = () => {
  const [activeTab, setActiveTab] = useState('flight');
  const [departureDate, setDepartureDate] = useState(null); // State to store the selected departure date
  const [checkInDate, setCheckInDate] = useState(null); // State to store the selected check-in date
  const [checkOutDate, setCheckOutDate] = useState(null); // State to store check-out date
  const [selectedLocation, setSelectedLocation] = useState(''); // State to store selected location for hotel
  const [numberOfGuests, setNumberOfGuests] = useState(1); // Number of guests for hotel booking
  const [numberOfRooms, setNumberOfRooms] = useState(1); // Number of rooms for hotel booking
  const [searchResult, setSearchResult] = useState(null); // State to handle the result after search

  const hotelLocations = [
    { name: 'The Leela Palace', city: 'New Delhi' },
    { name: 'Taj Mahal Palace', city: 'Mumbai' },
    { name: 'Oberoi Udaivilas', city: 'Udaipur' },
    { name: 'ITC Maurya', city: 'New Delhi' },
    { name: 'Le Meridien', city: 'Bangalore' },
    { name: 'Park Hyatt', city: 'Hyderabad' },
    { name: 'The Westin', city: 'Chennai' },
    { name: 'Trident', city: 'Kochi' },
  ];

  useEffect(() => {
    // Initialize flatpickr for date pickers every time the component renders or tab changes
    flatpickr('.date-picker', {
      dateFormat: 'd M Y', // Set the desired date format (example: day, month, year)
      allowInput: false, // Disable manual input (only allow calendar popup)
      clickOpens: true, // Enable the calendar to pop up when the input field is clicked
      onChange: (selectedDates, dateStr, instance) => {
        // If the input is for the departure date, update the state for departure
        if (instance.input.id === 'departure') {
          setDepartureDate(selectedDates[0]);
        }
        // If the input is for the return date, update the minDate for return field
        if (instance.input.id === 'return') {
          if (departureDate) {
            instance.set('minDate', departureDate); // Set the minimum date to the selected departure date
          }
        }
        // If the input is for the check-in date, update the state for check-in
        if (instance.input.id === 'check-in') {
          setCheckInDate(selectedDates[0]);
        }
        // If the input is for the check-out date, update the minDate for check-out field
        if (instance.input.id === 'check-out') {
          setCheckOutDate(selectedDates[0]);
        }
      },
    });
  }, [activeTab, departureDate, checkInDate, checkOutDate]); // Dependency on activeTab ensures re-initialization on tab change

  const flightAirports = [
    { code: 'DEL', name: 'Indira Gandhi International Airport (Delhi)' },
    { code: 'BOM', name: 'Chhatrapati Shivaji Maharaj International Airport (Mumbai)' },
    { code: 'BLR', name: 'Kempegowda International Airport (Bangalore)' },
    { code: 'CCU', name: 'Netaji Subhas Chandra Bose International Airport (Kolkata)' },
    { code: 'LHR', name: 'London Heathrow Airport (London)' },
    { code: 'JFK', name: 'John F. Kennedy International Airport (New York)' },
    { code: 'DXB', name: 'Dubai International Airport (Dubai)' },
    { code: 'SIN', name: 'Changi Airport (Singapore)' },
  ];

  const trainStations = [
    { code: 'NDLS', name: 'New Delhi Railway Station' },
    { code: 'BCT', name: 'Mumbai Central Railway Station' },
    { code: 'MAS', name: 'Chennai Egmore Railway Station' },
    { code: 'HZY', name: 'Howrah Junction (Kolkata)' },
    { code: 'SBC', name: 'Bengaluru City Railway Station' },
    { code: 'CBE', name: 'Coimbatore Junction' },
    { code: 'PNR', name: 'Pune Junction' },
    { code: 'LKO', name: 'Lucknow Junction' },
  ];

  const busStations = [
    { code: 'DEL', name: 'Delhi ISBT' },
    { code: 'BOM', name: 'Mumbai Dadar Bus Station' },
    { code: 'BLR', name: 'Bangalore KSRTC Bus Station' },
    { code: 'KOL', name: 'Kolkata ESRTC Bus Station' },
    { code: 'HYD', name: 'Hyderabad Bus Station' },
    { code: 'CHN', name: 'Chennai Mofussil Bus Terminus' },
    { code: 'CCU', name: 'Kolkata Esplanade Bus Station' },
    { code: 'PUN', name: 'Pune Shivajinagar Bus Station' },
  ];

  const handleSearch = () => {
    let resultMessage = '';
    if (activeTab === 'flight') {
      resultMessage = `You are looking for flights from ${flightAirports[0].name} to ${flightAirports[1].name} on ${departureDate}.`;
    } else if (activeTab === 'train') {
      resultMessage = `You are looking for trains from ${trainStations[0].name} to ${trainStations[1].name} on ${departureDate}.`;
    } else if (activeTab === 'bus') {
      resultMessage = `You are looking for buses from ${busStations[0].name} to ${busStations[1].name} on ${departureDate}.`;
    } else if (activeTab === 'hotel') {
      resultMessage = `You are looking for hotels in ${selectedLocation}, with check-in on ${checkInDate} and check-out on ${checkOutDate}.`;
    }
    setSearchResult(resultMessage);
  };

  return (
    <Container>
      <Row>
        <Col lg="6">
          <div className="hero__content">
            <div className="hero__subtitle d-flex align-items-center">
              <Subtitle subtitle={'Know Before You Go'} />
              <img src={worldImg} alt="" />
            </div>
            <h1>
              Travelling unlocks new places, experiences, and unforgettable memories.
            </h1>
            <p>
              Explore the world and experience its beauty and culture. We ensure every adventure is unforgettable and seamless.
            </p>
          </div>
        </Col>
        <Col lg="2">
          <div className="hero__img-box">
            <img src={heroImg} alt="" />
          </div>
        </Col>
        <Col lg="2">
          <div className="hero__img-box mt-4">
            <video src={heroVideo} alt="" controls />
          </div>
        </Col>
        <Col lg="2">
          <div className="hero__img-box mt-5">
            <img src={heroImg02} alt="" />
          </div>
        </Col>
      </Row>

      {/* Tabs for selecting booking type */}
      <div className="tabs">
        <button
          className={`tab ${activeTab === 'flight' ? 'active' : ''}`}
          onClick={() => setActiveTab('flight')}
        >
          Flight
        </button>
        <button
          className={`tab ${activeTab === 'train' ? 'active' : ''}`}
          onClick={() => setActiveTab('train')}
        >
          Train
        </button>
        <button
          className={`tab ${activeTab === 'bus' ? 'active' : ''}`}
          onClick={() => setActiveTab('bus')}
        >
          Bus
        </button>
        <button
          className={`tab ${activeTab === 'hotel' ? 'active' : ''}`}
          onClick={() => setActiveTab('hotel')}
        >
          Hotel
        </button>
      </div>

      {/* Conditional rendering based on selected tab */}
      {activeTab === 'flight' && (
        <div className="booking-form">
          <h2>Flight Booking</h2>
          <div className="form-group">
            <div className="radio-group">
              <input type="radio" id="one-way" name="trip-type" checked />
              <label htmlFor="one-way">One Way</label>
            </div>
            <div className="radio-group">
              <input type="radio" id="round-trip" name="trip-type" />
              <label htmlFor="round-trip">Round Trip</label>
            </div>
            <div className="radio-group">
              <input type="radio" id="multi-city" name="trip-type" />
              <label htmlFor="multi-city">Multi City</label>
            </div>
          </div>
          <div className="form-group horizontal-form-group">
            <div className="dropdown">
              <label htmlFor="from">From</label>
              <select id="from">
                {flightAirports.map((airport) => (
                  <option key={airport.code} value={airport.code}>
                    {airport.name}
                  </option>
                ))}
              </select>
            </div>
            <div className="dropdown">
              <label htmlFor="to">To</label>
              <select id="to">
                {flightAirports.map((airport) => (
                  <option key={airport.code} value={airport.code}>
                    {airport.name}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label htmlFor="departure">Departure</label>
              <input type="text" id="departure" className="date-picker" placeholder="Select departure date" />
            </div>
            <div>
              <label htmlFor="return">Return</label>
              <input type="text" id="return" className="date-picker" placeholder="Select return date" />
            </div>
          </div>
          <button className="search-btn" onClick={handleSearch}>SEARCH</button>
        </div>
      )}

      {activeTab === 'train' && (
        <div className="booking-form">
          <h2>Train Booking</h2>
          <div className="form-group horizontal-form-group">
            <div className="dropdown">
              <label htmlFor="train-from">From</label>
              <select id="train-from">
                {trainStations.map((station) => (
                  <option key={station.code} value={station.code}>
                    {station.name}
                  </option>
                ))}
              </select>
            </div>
            <div className="dropdown">
              <label htmlFor="train-to">To</label>
              <select id="train-to">
                {trainStations.map((station) => (
                  <option key={station.code} value={station.code}>
                    {station.name}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label htmlFor="train-departure">Departure</label>
              <input type="text" id="train-departure" className="date-picker" placeholder="Select departure date" />
            </div>
            <div className="train-class">
              <label htmlFor="train-class">Train Class</label>
              <select id="train-class">
                <option value="Sleeper">Sleeper</option>
                <option value="2AC">2AC</option>
                <option value="3AC">3AC</option>
                <option value="1AC">1AC</option>
              </select>
            </div>
          </div>
          <button className="search-btn" onClick={handleSearch}>SEARCH</button>
        </div>
      )}

      {activeTab === 'bus' && (
        <div className="booking-form">
          <h2>Bus Booking</h2>
          <div className="form-group horizontal-form-group">
            <div className="dropdown">
              <label htmlFor="bus-from">From</label>
              <select id="bus-from">
                {busStations.map((station) => (
                  <option key={station.code} value={station.code}>
                    {station.name}
                  </option>
                ))}
              </select>
            </div>
            <div className="dropdown">
              <label htmlFor="bus-to">To</label>
              <select id="bus-to">
                {busStations.map((station) => (
                  <option key={station.code} value={station.code}>
                    {station.name}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label htmlFor="bus-departure">Departure</label>
              <input type="text" id="bus-departure" className="date-picker" placeholder="Select departure date" />
            </div>
          </div>
          <button className="search-btn" onClick={handleSearch}>SEARCH</button>
        </div>
      )}

      {activeTab === 'hotel' && (
        <div className="booking-form">
          <h2>Hotel Booking</h2>
          <div className="form-group">
            <div className="dropdown">
              <label htmlFor="hotel-location">Hotel Location</label>
              <select
                id="hotel-location"
                value={selectedLocation}
                onChange={(e) => setSelectedLocation(e.target.value)}
              >
                <option value="">Select Location</option>
                {hotelLocations.map((hotel, index) => (
                  <option key={index} value={hotel.name}>
                    {hotel.name} ({hotel.city})
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label htmlFor="check-in">Check-in</label>
              <input type="text" id="check-in" className="date-picker" placeholder="Select check-in date" />
            </div>
            <div>
              <label htmlFor="check-out">Check-out</label>
              <input type="text" id="check-out" className="date-picker" placeholder="Select check-out date" />
            </div>
            <div className="guests-rooms">
              <div>
                <label htmlFor="guests">Guests</label>
                <input
                  type="number"
                  id="guests"
                  min="1"
                  value={numberOfGuests}
                  onChange={(e) => setNumberOfGuests(e.target.value)}
                />
              </div>
              <div>
                <label htmlFor="rooms">Rooms</label>
                <input
                  type="number"
                  id="rooms"
                  min="1"
                  value={numberOfRooms}
                  onChange={(e) => setNumberOfRooms(e.target.value)}
                />
              </div>
            </div>
          </div>
          <button className="search-btn" onClick={handleSearch}>SEARCH</button>
        </div>
      )}

      {/* Handle Search Result Section */}
      {searchResult && (
        <div className="search-result">
          <h3>Search Result</h3>
          <p>{searchResult}</p>
        </div>
      )}
    </Container>
  );
};

export default Home;