import React from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import './App.css';
import Layout from './components/Layout/Layout'; // Your Layout component
import Routers from './router/Routers'; // Your Routers component

function App() {
  return (
    <Router>
      <Layout>
        <Routers />  {/* Add Routers here to handle routing */}
      </Layout>
    </Router>
  );
}

export default App;
